<template>
  <div id="app" style="background: white">
    <form class="form-horizontal" >
        <div class="row">
            <div class="col-md-3">
                <div class="form-group">
                     <label class="col-sm-4 control-label">Gender: </label>
                      <div class="col-sm-6">
                        <input type="text" class="form-control" v-model="gender">
                      </div>
                  </div>
            </div>
            <div class="col-md-3">
                <div class="form-group">
                     <label class="col-sm-4 control-label">Age: </label>
                      <div class="col-sm-6">
                        <input type="text" class="form-control" v-model="age">
                      </div>
                  </div>
            </div>
            <div class="col-md-3">
                <div class="form-group">
                     <label class="col-sm-4 control-label">Country: </label>
                      <div class="col-sm-6">
                        <input type="text" class="form-control" v-model="country">
                      </div>
                  </div>
            </div>
            <div class="col-md-3">
                  <div class="form-group">
                     <label class="col-sm-4 control-label">Signup: </label>
                      <div class="col-sm-6">
                        <input type="text" class="form-control" v-model="signup">
                      </div>
                  </div>
            </div>
        </div>





      <div class="row">
        Choosed Songs:
        <p v-for="s in history">{{s.s2}} , {{s.num}}</p>
      </div>

      <div class="form-group">
         <label class="col-sm-4 control-label">Songs Filter: </label>
          <div class="col-sm-4">
            <input type="text" class="form-control" v-model="f">
          </div>
        <input type="button" class="btn btn-info" value="search" v-on:click="get_candidate()">
        <input type="button" class="btn btn-info" value="random_history" v-on:click="random_history()">
      </div>

      <div>Candidates:
          <div class="row" v-for="c in candidates">
            <div class="col-sm-4">
              {{c.s2}}
            </div>
            <div class="col-sm-2">
              <input type="number" v-model="c.num">
            </div>
            <div class="col-sm-2">
              <input type="button" value="Add" v-on:click="add_to_history(c)">
            </div>
          </div>
      </div>

      <div  class="row">

        Algorithm: <select name="" id="" v-model="algorithm">
        <option value="random">Random</option>
        <option value="knn">KNN</option>
        <option value="svd">SVD</option>

      </select>
        Size: <input type="number" v-model="num">
        <input type="button" value="Recommend Some!" class="btn btn-info" v-on:click="recommend_some()">
      </div>
    </form>

    <div class="row" style="margin-top: 20px; margin-bottom: 10px">
      <div class="col-md-6">
        <h4>Recommend Songs：</h4>
        <p v-for="s in recommend">{{s}}</p>
      </div>
      <div class="col-md-6">
        <h4>Recommend Artists：</h4>
        <p v-for="s in artist">{{s}}</p>
      </div>

    </div>
    <div>
      <sweet-modal :blocking=true :hide-close-button=true ref="Alert">Recommending...</sweet-modal>
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
export default {
  name: 'App',
  data() {
    return {
      algorithm: 'random',
      gender: '',
      age: '',
      country: '',
      signup: '',
      f: '',
      history: [],
      recommend: [],
      num: 10,
      candidates: [],
      artist: [],
    }
  },
  methods: {

    get_candidate(){
      this.$http.post('/api/candidate', {'key': this.f}).then(
        (response) => {
          this.candidates = response.data.candidates;
        },
        (response) => {}
      )
    },
    add_to_history(item){
        this.history.push(item);
    },
    random_history(){
      this.$http.post('/api/random_history').then(
        (response) => {
            this.history = response.data.history;

        },
        (response) => {

        }
      )
    },
    recommend_some() {
      this.$refs.Alert.open()
      this.$http.post('/api/recommend', {
        'algorithm': this.algorithm,
        'gender': this.gender,
        'age': this.age,
        'country': this.country,
        'signup': this.signup,
        'history': this.history,
        'num': this.num
      }).then(
            (response) => {
                this.recommend = response.data.recommend;
                this.artist = response.data.artist;
                this.$refs.Alert.close()
            },
            function (response) {

            }
        )
    }
  },
  components: {
    HelloWorld,SweetModal,
		SweetModalTab
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



